.container{
    width: 320px;
    padding-left: 20px;
    padding-right: 20px;
    @media screen and (min-width: 480px) {
        padding: 0px 20px;
            width: 480px;
            height: 100%;
        }
    
        @media screen and (min-width: 768px) {
            padding: 0px 33px;
            width: 768px;
            height: 100%;
        }
    
        @media screen and (min-width: 1200px) {
            width: 1200px;
            height: 100%;
        }
}